/*---------------------------------------------------------------------------------------------
*
*  Fonts
*
*---------------------------------------------------------------------------------------------*/
@font-face {
	font-family: 'Inter';
	src: url('../../inc/fonts/Inter-Regular.woff2') format('woff2'),
	 	url('../../inc/fonts/Inter-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Inter';
	src: url('../../inc/fonts/Inter-Medium.woff2') format('woff2'),
	 	url('../../inc/fonts/Inter-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
}

/*---------------------------------------------------------------------------------------------
*
*  Global
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group #wpcontent {
	font-family: 'Inter', system-ui;
	line-height: 140%;
}

/*---------------------------------------------------------------------------------------------
*
*  Links
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	a {
		color: $blue-500;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Headings
*
*---------------------------------------------------------------------------------------------*/
.h1 {
	font-size: 21px;
	font-weight: 400;
}

.h2 {
	font-size: 18px;
	font-weight: 400;
}

.h3 {
	font-size: 16px;
	font-weight: 400;
}

.post-type-acf-field-group,
.acf-headerbar {

	h1 {
		@extend .h1;
	}

	h2 {
		@extend .h2;
	}

	h3 {
		@extend .h3;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Paragraphs
*
*---------------------------------------------------------------------------------------------*/
.p1 {
	font-size: 15px;
}

.p2 {
	font-size: 14px;
}

.p3 {
	font-size: 13.5px;
}

.p4 {
	font-size: 13px;
}

.p5 {
	font-size: 12.5px;
}

.p6 {
	font-size: 12px;
}

.p7 {
	font-size: 11.5px;
}

.p8 {
	font-size: 11px;
}

/*---------------------------------------------------------------------------------------------
*
*  Page titles
*
*---------------------------------------------------------------------------------------------*/
.acf-page-title {
	@extend .h2;
	color: $gray-700;
}

/*---------------------------------------------------------------------------------------------
*
*  Hide old / native WP titles from pages
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	.acf-settings-wrap h1,
	#acf-admin-tools h1 {
		display: none;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Small
*
*---------------------------------------------------------------------------------------------*/
.acf-small {
	@extend .p6;
}